import { PresentationLoan } from '@backbase/data-ang/loans';

export enum LoanPaymentInfoType {
  ScheduledNext = 'ScheduledNext',
  ScheduledLast = 'ScheduledLast',
  Overdue = 'Overdue',
  FullyPaid = 'FullyPaid',
}

export type LoanPaymentInfoDataConfig<T extends PresentationLoan = PresentationLoan> = (item: T) => LoanPaymentInfoData;

export interface LoanPaymentInfoData {
  amount?: string;
  date?: string;
  paymentType: LoanPaymentInfoType;
}
